<script>

export default {
  data: () => ({
    tracking_number: 'SM601097311SG',
  }),
  computed: {
    today() {
      const time_list = new Date().toISOString().split('T')[0].replaceAll('-', '/').split('/')
      return time_list[2] + '/' + time_list[1] + '/' + time_list[0]
    },
    update_time() {
      const time_list = new Date().toISOString().split('T')[1].split(':')
      return time_list[0] + ':' + time_list[1]
    },
    step_text(){
      if(this.$route.path==='/'){
        return 'Verify Address'
      }else if(this.$route.path === '/payment'){
        return  'Verify Payment'
      }else if(this.$route.path === '/confirm'){
        return  'Verify One Time Code'
      }else {
        return ''
      }
    }
  },
  methods:{
    BackTop(){
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
  }
}
</script>
